<template>
  <div class="integral-list">
    <div class="info">账户剩余积分：{{integral}}</div>
    <div class="panel">
      <div class="text">邀请一个新用户赠送<font color="#FF0000">50积分</font></div>
      <div class="text">邀请链接：<a :href="'http://www.baidu.com/' + inviteCode">http://www.baidu.com/{{inviteCode}}</a> </div>
      <div class="text">邀请码：{{inviteCode}}</div>
      <div class="text">邀请记录：</div>
      <template v-loading="loading">
        <div class="panel" v-for="(item, index) in dataArray" :key="index">
          <div class="title">{{item}} - 成功邀请 - 50积分已发放</div>
        </div>
      </template>
      <el-pagination
        background
        layout="->, prev, pager, next"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        :total="total">
      </el-pagination>
    </div>
    <div class="panel" style="display: flex;">
      <div class="text">联系客服充值积分：</div>
      <img :src="img_url">
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode'
import { fetchUserInfo, fetchInviteRecordInfo } from '@/api/tkpro'
export default {
  name: 'integral-list',
  data() {
    return {
      img_url: '',
      integral: 0,
      inviteCode: '',
      loading: false,
      name: '',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dataArray: [],
    }
  },
  created(){
    this.loadData()
  },
  methods:{
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      currentPage = val
      this.loadData()
      console.log(`当前页: ${val}`);
    },
    loadData () {
      QRCode.toDataURL('stormfa is nice').then(url => {
        this.img_url = url
      }).catch(err => {
        console.error(err)
      })
      fetchUserInfo('stormfa').then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.integral = res.data.rewardPoint
        this.inviteCode = res.data.inviteCode
      }).catch(error => {
        console.log(error);
      })
      this.loading = true
      fetchInviteRecordInfo(this.name, this.currentPage).then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.dataArray = res.data.list
        this.total = res.data.total
        // this.pageSize = res.data.pageSize
        console.log(JSON.parse(JSON.stringify(this.dataArray)));
        this.loading = false
      }).catch(error => {
        console.log(error);
        this.loading = false
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.integral-list {
  background: #eeeeee;
  padding: 20px;
  .info {
  }
  .panel {
    background-color: rgba(255, 255, 255, 100);
    color: rgba(16, 16, 16, 100);
    font-size: 14px;
    border: 1px solid rgba(187, 187, 187, 100);
    padding: 22px 20px;
    margin: 16px 0;
    .text {
      margin-bottom: 16px;
      color: rgba(16, 16, 16, 100);
      font-size: 20px;
    }
    img {
      width: 200px;
      height: 200px;
    }
  }
}
</style>



